import { Bullet, Fish } from './../module_basic/shared/protocols/public/fish/Fish';
import { _decorator, Component, Node } from 'cc';
import { GameMgr } from 'db://assets/module_basic/scripts/GameMgr';
import { RoomMgr } from 'db://assets/module_basic/scripts/RoomMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { gameNet } from '../module_basic/scripts/NetGameServer';
import { MsgNewFish } from '../module_basic/shared/protocols/public/fish/MsgNewFish';
import { MsgRoomFish } from '../module_basic/shared/protocols/public/fish/MsgRoomFish';
import { MsgFishDead } from '../module_basic/shared/protocols/public/fish/MsgFishDead';
import { MsgFishWave } from '../module_basic/shared/protocols/public/fish/MsgFishWave';
import { MsgNewFishGroup } from '../module_basic/shared/protocols/public/fish/MsgNewFishGroup';
import { App } from './script/core/app';
import { ResLoadHelper } from './script/utils/resLoadHelper';
import { AssetManager } from 'cc';
import { assetManager } from 'cc';
import { Logger } from '../core_tgx/base/log/Logger';
import { ResFishConfig } from '../module_basic/shared/protocols/public/fish/PtlFishConfig';
import { ApiReturn } from 'tsrpc-browser';
const { ccclass, property } = _decorator;

@ccclass('FishWhaleGameMgr')
export class FishWhaleGameMgr extends GameMgr {
    private static _inst: FishWhaleGameMgr;
    private _fishList: Array<MsgNewFish>; //鱼
    private _fishGroup:MsgNewFishGroup;
    private _getData:boolean;
    private _bulletList:Array<Bullet>;
    private _lastBulletId:number;
    //从服务器获取的配置信息
    public fishConfig:ResFishConfig;
    private _preloadResIndex:number = 0;
    private _preloadResTotal:number;
    public static get inst(): FishWhaleGameMgr {
        if (!this._inst) {
            this._inst = new FishWhaleGameMgr();
        }
        return this._inst;
    }

    constructor() {
        super();
        this._gameType = SubGameDef.FISH_WHALE;
        this._getData = false;
        this._gameScene = { name: 'game_fish_whale', bundle: ModuleDef.FISH_WHALE };
        this._loadingScene = { name: 'subgame_loading', bundle: ModuleDef.BASIC };
       
    }

    override init() {
        ResLoadHelper.setBundle(assetManager.getBundle(ModuleDef.FISH_WHALE));
        this._fishList = [];
        this._bulletList = [];
        this._lastBulletId = 0;
        gameNet.listenMsg("fish/NewFish", this.onNet_NewFish, this);
        gameNet.listenMsg("fish/RoomFish", this.onNet_RoomFish, this);
        gameNet.listenMsg("fish/FishWave", this.onNet_FishWave, this);
        gameNet.listenMsg("fish/NewFishGroup", this.onNet_FishGroup, this);
    }

    override uinit() {
        this._getData = false;
        gameNet.unlistenMsgAllByThis(this);
    }
    /**获取数据 获取过数据后 清空 为了场景没加载好，漏掉消息*/
    public getInitData():any[]
    {
        this._getData = true;
        var result:any[] = [this._fishList, this._fishGroup,this._bulletList,this._lastBulletId];
        return result;
    }


    private onNet_NewFish(msg:MsgNewFish)
    {
        if(this._getData)
        {
            return;
        }
        this._fishList.push(msg);
    }
    private onNet_RoomFish(msg:MsgRoomFish)
    {
        if(this._getData)
        {
            return;
        }
        for(var i:number = 0;i<msg.fishes.length;i++)
        {
            this.onNet_NewFish(msg.fishes[i]);
        }
        if(msg.bullets)
        {
            for(var j:number = 0;j<msg.bullets.length;j++)
            {
                this._bulletList.push(msg.bullets[j]);
            }
        }
        if(msg.lastBulletId)
        {
            this._lastBulletId = Number(msg.lastBulletId);
        }
        
    }
    private onNet_FishWave(msg:MsgFishWave)
    {
        if(this._getData)
        {
            return;
        }
        this._fishGroup = null;
        this._fishList = [];
    }
    private onNet_FishGroup(msg:MsgNewFishGroup)
    {
        if(this._getData)
        {
            return;
        }
        this._fishGroup = msg;
    }
    /**
     * 加载资源
     */
    public override async preLoadRes(processFun:Function):Promise<boolean> {
        this._preloadResIndex = 0;
        this._preloadResTotal = 6;
        processFun(0);
        await this.loadResource(processFun);
        await this.preLoadGameScene(processFun);
        return true;
    }
    public async loadResource(processFun:Function):Promise<boolean>
    {
        Logger.trace("load preload resource start:"+SubGameDef.FISH_WHALE)
        let url1 = [
            "./prefab/ui/gameView",
        ];
        await new Promise((resolve, reject) => {
            ResLoadHelper.preloadPath(url1, null, () => {
                // 加载成功，调用 resolve 通知 Promise 操作完成
                resolve(true);
            }, (err) => {
                // 加载出错，调用 reject 并传递错误信息
                reject(err);
            });
        });
        this._preloadResIndex ++;
        processFun(this._preloadResIndex/this._preloadResTotal);
        let url2 = [
            "./prefab/pao/bullet",
        ];
        await new Promise((resolve, reject) => {
            ResLoadHelper.preloadPath(url2, null, () => {
                // 加载成功，调用 resolve 通知 Promise 操作完成
                resolve(true);
            }, (err) => {
                // 加载出错，调用 reject 并传递错误信息
                reject(err);
            });
        });
        this._preloadResIndex ++;
        processFun(this._preloadResIndex/this._preloadResTotal);
        let url3 = [
            "./prefab/pao/pao",
        ];
        await new Promise((resolve, reject) => {
            ResLoadHelper.preloadPath(url3, null, () => {
                // 加载成功，调用 resolve 通知 Promise 操作完成
                resolve(true);
            }, (err) => {
                // 加载出错，调用 reject 并传递错误信息
                reject(err);
            });
        });
        this._preloadResIndex ++;
        processFun(this._preloadResIndex/this._preloadResTotal);
        let url4 = [
            "./prefab/pao/fishNet",
        ];
        await new Promise((resolve, reject) => {
            ResLoadHelper.preloadPath(url4, null, () => {
                // 加载成功，调用 resolve 通知 Promise 操作完成
                resolve(true);
            }, (err) => {
                // 加载出错，调用 reject 并传递错误信息
                reject(err);
            });
        });
        this._preloadResIndex ++;
        processFun(this._preloadResIndex/this._preloadResTotal);
        await ResLoadHelper.loadBullet();
        this._preloadResIndex ++;
        processFun(this._preloadResIndex/this._preloadResTotal);
        Logger.trace("load preload resource end:"+SubGameDef.FISH_WHALE)
        return true;
    }
    private async preLoadGameScene(processFun:Function):Promise<boolean> {
        return new Promise((resolve, reject) => {
            let bundle = assetManager.getBundle(ModuleDef.FISH_WHALE);
            if(bundle)
            {
                bundle.preloadScene(SubGameMgr.gameMgr.gameScene.name,null,function ()
                {
                    processFun(1);
                    resolve(true);
                }.bind(this));  //7
                
            }
            else
            {
                resolve(false);
            }
        });
        
    }
    /**
     * 请求服务器配置
     * @returns 
     */
    public async requestFishConfig():Promise<boolean>
    {
         let cfg:ApiReturn<ResFishConfig> = await gameNet.callApi("fish/FishConfig", {});
         this.fishConfig = cfg.isSucc?cfg.res:null;
         return true;
    }
    /** 
     * 获取子弹的金币消耗
    */
    public getBulletCoin(bulletLev:number):number
    {
        if(this.fishConfig)
        {
            for(var i:number = 0;i<this.fishConfig.bullets.length;i++)
            {
                if(this.fishConfig.bullets[i].bulletLevel == bulletLev)
                {
                    return this.fishConfig.bullets[i].needGolds;
                }
            }
        }
        return -1;
    }

}

SubGameMgr.inst.registerGameMgr(FishWhaleGameMgr.inst);


