/** 所有窗口名字 */
export namespace ViewName {
    /** 只能单独存在 */
    export enum Single {
        /** test */
        eTest = 'ui/testView',
        /** 主界面 */
        eHomeView = 'ui/homeView',
        /** 游戏界面 */
        eGameView = 'ui/gameView',
        /** 结算界面 */
        eResultView = 'ui/resultView',
        /** 设置界面 */
        eSettingView = 'ui/settingView',
        /** 暂停界面 */
        ePaurseView = 'ui/paurseView',
        /** 加载界面 */
        eLoadingView = 'ui/loadingView',
        /** 领取奖励界面 */
        eGoldRewrdView = 'ui/goldRewrdView',
        /** 分享界面 */
        eShareView = 'ui/shareView',
        /** 购买界面 */
        eBuyView = 'ui/buyView',
        /** 目标提示 */
        eLevelTargetView = 'ui/levelTargetView',
        /** 游戏中的设置 */
        esettingGameView = 'ui/settingGameView',
        /** 转盘 */
        eLuckSpinView = 'ui/luckSpinView',
        /** 中奖 */
        eRewardView = 'ui/rewardView',
        /** 签到 */
        eSiginView = 'ui/siginView',
        /** 在线奖励 */
        eOnlineRewardView = 'ui/onlineRewardView',
        /** 关卡目标 */
        eTargetView = 'ui/targetView',
        eFeedView = 'ui/feedView',
        eHelpView = 'ui/helpView',
    }

    /** 可以多个同时存在 */
    export enum Multiple {
        /** 提示框 */
        eAlert = 'common/alertView',
        /** 飘字提示 */
        eMsg = 'ui/tipsView',
    }

    export enum Pieces {
        particle = "particle",
        grid = "grid",
        score = "score",
    }

}
