import { instantiate, Prefab, tween, v3, Vec3 } from "cc";
import { EventName } from "../const/eventName";
import { ViewName } from "../const/viewNameConst";
import { App } from "../core/app";
import { ResLoadHelper } from "./resLoadHelper";
import { StorageHelper, StorageHelperKey } from "./storageHelper";

/**
 * 全局函数，复用多的功能函数独立出来
 */
class Helper {
    private debugInfo: string[] = [];
    public index: number = 0;

    /** debug info */
    pushDebugInfo(str: string) {
        this.debugInfo.push(str);
        if (this.debugInfo.length > 10000) {
            this.debugInfo.shift();
        }
    }

    getDebugInfo(): string[] {
        return this.debugInfo;
    }

    clearDebugInfo() {
        this.debugInfo.splice(0);
    }

    /** create and get index */
    public getTileIndex() {
        return this.index++;
    }

    getGold() {
        return +StorageHelper.getData(StorageHelperKey.Gold);
    }

    setGold(gold: number) {
        let curent = +this.getGold();
        StorageHelper.setData(StorageHelperKey.Gold, (gold + curent) + "");
    }
    /** 显示失败界面 */
    showLoseView() {
        App.view.openView(ViewName.Single.eResultView, false);
    }

    public bezierTo(target: any, duration: number, c1: Vec3, c2: Vec3, to: Vec3, opts?: any) {
        opts = opts || Object.create(null);
        /*
        * @desc 二阶贝塞尔
        * @param {number} t 当前百分比
        * @param {} p1 起点坐标
        * @param {} cp 控制点
        * @param {} p2 终点坐标
        * @returns {any}
        */
        let twoBezier = (t: number, p1: Vec3, cp: Vec3, p2: Vec3) => {
            let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
            let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
            return v3(x, y, 1);
        };
        opts.onUpdate = (arg: Vec3, ratio: number) => {
            target.position = twoBezier(ratio, c1, c2, to);
        };
        return tween(target).to(duration, {}, opts)
    }


    private fishNetPre: Prefab = null;
    /** 渔网 */
    async loadFishNet() {
        if (!this.fishNetPre) {
            this.fishNetPre = await ResLoadHelper.loadFishNet();
        }
        let net = instantiate(this.fishNetPre);
        return net;
    }
    public clearFishNET() {
        if(this.fishNetPre) this.fishNetPre = null;
    }
    /** 获取炮台等级 */
    getPaoLevel() {
        return +StorageHelper.getData(StorageHelperKey.PaoLevel, 1);
    }
    setPaoLevel(isAdd: boolean) {
        let lv = this.getPaoLevel();
        if (isAdd) {
            lv++;
            if (lv > 13) {
                lv = 1;
            }
        }
        else {
            lv--;
            if (lv < 1) {
                lv = 13;
            }
        }
        StorageHelper.setData(StorageHelperKey.PaoLevel, lv);
        App.gameLogic.paoLevel = lv;
        App.event.emit(EventName.Game.ChangePaotai);
    }
}

export let GlobalFuncHelper = new Helper();




