import { _decorator, Component, Node, Collider2D, BoxCollider2D, Skeleton, Vec2, v2, tween, bezier, Vec3, v3, sp, Tween, Contact2DType, IPhysics2DContact, RigidBody2D, Label } from 'cc';
import { BaseNodeCmpt } from '../../components/baseNodeCmpt';
import { EventName } from '../../const/eventName';
import { App } from '../../core/app';
import { getQuadrantDegree } from '../../net/util';
import { GlobalFuncHelper } from '../../utils/globalFuncHelper';
import { BYFishRoute } from './fishRoute';
import { bulletCmpt } from './bulletCmpt';

const { ccclass, property } = _decorator;

@ccclass('BYFish')
export class BYFish extends Component {

    public isDieing: boolean = false;
    public fishId: number = 0;
    public fishFormationId: number = -1;
    public rootIdx = -1;
    public typeId: number = 0;

    private collider: Collider2D = null;
    public ice: Node = null;

    public lastPos: Vec2 = undefined;
    private skelen: sp.Skeleton = null;
    private tweenTarget: any;

    onLoad() {
        this.skelen = this.node.getComponent(sp.Skeleton);
        this.initColliderAndShader();
        this.showBtn()
        this.ice = this.node.getChildByName("ice");
        let lb = this.node.getChildByName("lb");
        if (lb) {
            lb.active = false;
        }
    }

    update() {
        if (this.rootIdx != -1) {
            this.updateRotation();
        }
    }

    showBtn() {
        if (this.node.getChildByName("btn"))
            this.node.getChildByName("btn").active = App.gameLogic.isLock;
    }

    initColliderAndShader() {
        this.collider = this.node.getComponent(Collider2D);
        if (!this.collider) {
            this.collider = this.node.getComponent(BoxCollider2D);
        }
        if (this.collider) {
            this.collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            this.collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体开始接触时被调用一次
        if (otherCollider.node.name == "bullet" && !this.isDieing) {
            this.showFishNet();

            let bulletCmpt1 = otherCollider.node.getComponent(bulletCmpt);
            if(!bulletCmpt1.isDistoried)
            {
                App.event.emit(EventName.Game.HitFish,bulletCmpt1.bulletId,bulletCmpt1.bulletLev,this.fishId);
                bulletCmpt1.isDistoried = true;
                bulletCmpt1.chgColliderState(false);
                this.scheduleOnce(() => {
                    App.event.emit(EventName.Game.BulletDied);
                    bulletCmpt1.node.destroy();
                }, 0.01);

            }
        }
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        // console.log('onEndContact');
    }

    chgColliderState(state: boolean) {
        if (this.collider) {
            this.collider.enabled = state;
            this.node.getComponent(RigidBody2D).enabledContactListener = state;
        }
    }

    chgIceState(state: boolean) {
        if (this.ice) {
            this.ice.active = state;
        }
    }

    chgSpineState(state: boolean) {

    }

    showTest() {
        let lb = this.node.getChildByName("lb");
        if (lb) {
            lb.active = false;
            if (App.gameLogic.isTest) {
                lb.active = true;
                lb.getComponent(Label).string = "" + this.rootIdx;
            }
        }
    }

    public move(offset: Vec2, startPosNum: number, firstTime: number) {
        this.showTest();
        //记录第一个坐标
        this.lastPos = v2(BYFishRoute.anchor[this.rootIdx].points[0][0] + offset.x, BYFishRoute.anchor[this.rootIdx].points[0][1] + offset.y);
        if ((this.lastPos.x > 0 || this.rootIdx == 57) && this.rootIdx != 58) {
            this.node.setScale(v3(-1, 1))
        } else {
            this.node.setScale(v3(1, 1))
        }

        let lineCount = BYFishRoute.anchor[this.rootIdx].points.length / 2;
        let actionArr: any[] = [];

        this.node.setPosition(v3(BYFishRoute.anchor[this.rootIdx].points[0][0] + offset.x, BYFishRoute.anchor[this.rootIdx].points[0][1] + offset.y, 1))
        for (let i = startPosNum; i < lineCount; i++) {
            let c1 = v3(BYFishRoute.control[this.rootIdx].points[i * 2][0] + offset.x, BYFishRoute.control[this.rootIdx].points[i * 2][1] + offset.y, 1);
            let c2 = v3(BYFishRoute.control[this.rootIdx].points[i * 2 + 1][0] + offset.x, BYFishRoute.control[this.rootIdx].points[i * 2 + 1][1] + offset.y, 1);
            let a2 = v3(BYFishRoute.anchor[this.rootIdx].points[(i + 1) * 2 - 1][0] + offset.x, BYFishRoute.anchor[this.rootIdx].points[(i + 1) * 2 - 1][1] + offset.y, 1);
            let bezier = [c1, c2, a2];
            // let bezierTo = GlobalFuncHelper.bezierTo(this.node, BYFishRoute.anchor[this.rootIdx].curveTime, c1, c1, a2);
            let bezierTo = tween(this.node).to(BYFishRoute.anchor[this.rootIdx].curveTime, { position: a2 });
            actionArr.push(bezierTo);
        }
        // for (let i = startPosNum; i < Math.floor(lineCount); i++) {
        //     let c1 = v3(BYFishRoute.control[this.rootIdx].points[i * 2][0] + offset.x, BYFishRoute.control[this.rootIdx].points[i * 2][1] + offset.y, 1);
        //     let c2 = v3(BYFishRoute.control[this.rootIdx].points[i * 2 + 1][0] + offset.x, BYFishRoute.control[this.rootIdx].points[i * 2 + 1][1] + offset.y, 1);
        //     let a2 = v3(BYFishRoute.anchor[this.rootIdx].points[(i + 1) * 2 - 1][0] + offset.x, BYFishRoute.anchor[this.rootIdx].points[(i + 1) * 2 - 1][1] + offset.y, 1);
        //     console.log(c1.x, c1.y, c2.x, c2.y);
        //     let bezier = [c1, c2, a2];
        //     let bezierTo = this.bezierTo(this.node, BYFishRoute.anchor[this.rootIdx].curveTime, c1, c2, a2);
        //     actionArr.push(bezierTo);
        // }

        this.startMove(actionArr)
    }

    private index = 0;
    startMove(arr: any[]) {
        this.tweenTarget = arr[this.index];
        arr[this.index].call(() => {
            this.index++;
            if (this.index < arr.length) {
                this.startMove(arr);
            }
            else {
                // this.node.destroy();
                this.index = 0;
                let rount = BYFishRoute.anchor;
                let routeId = Math.floor(Math.random() * (rount.length - 1));
                this.rootIdx = routeId;
                this.move(v2(), 0, 0);
            }
        }).start();
    }

    private updateRotation() {
        // if ((this.typeId == BYFishMgr.jellyfishType) || this.typeId == BYFishMgr.seahorseType || this.typeId == BYFishMgr.bombType) {
        //     // this.node.angle = 0;
        //     return;
        // }
        let curPoint: Vec2 = v2(this.node.position.x, this.node.position.y);//获取鱼当前坐标
        if (this.lastPos == undefined) {
            return;
        }
        if (curPoint.x == this.lastPos.x || curPoint.y == this.lastPos.y) {
            return;
        }

        let deg: number = getQuadrantDegree(this.lastPos, curPoint);

        this.node.angle = deg;  //待修改 角度有问题

        this.lastPos = curPoint;  //保存当前的坐标给下一轮刷新使用
    }


    private count = 0;
    /** 点击鱼 */
    onClick_btn() {
        this.count++;
        // if (this.count % 2 != 0) {
        //     this.pauseMove();
        // }
        // else {
        //     this.resumeMove();
        // }
    }

    pauseMove() {
        if(this.tweenTarget) this.tweenTarget.stop();
    }
    resumeMove() {
        if(this.tweenTarget) this.tweenTarget.start();
    }

    playWalk() {
        this.skelen.setAnimation(1, "walk", true);
    }
    async showFishNet()
    {
        let net = await GlobalFuncHelper.loadFishNet();
        net.setPosition(this.node.position);
        this.node.parent.parent.addChild(net);
    }

    async playDie() {
        this.pauseMove();
        this.skelen.setAnimation(1, "died", true);
        this.isDieing = true;
      
        this.scheduleOnce(() => {
            App.event.emit(EventName.Game.FishDied, this.fishId);
            this.destroyFish();
        }, 1);
    }
    public destroyFish()
    {
        this.node.removeFromParent();
        this.node.parent = null;
        this.node.destroy();
    }

    stopAction() {
        this.skelen.paused = true;
    }
}