import { _decorator, Component, Node, Prefab, instantiate, Vec3, v2, UITransform, Event } from 'cc';
import { Constant } from '../../const/enumConst';
import { EventName } from '../../const/eventName';
import { App } from '../../core/app';
import { ResLoadHelper } from '../../utils/resLoadHelper';
import { bulletCmpt } from './bulletCmpt';
import { MsgFire } from 'db://assets/module_basic/shared/protocols/public/fish/MsgFire';
import { gameNet } from 'db://assets/module_basic/scripts/NetGameServer';
import { MsgHitFish } from 'db://assets/module_basic/shared/protocols/public/fish/MsgHitFish';
import { director } from 'cc';
import { view } from 'cc';
import { Bullet } from 'db://assets/module_basic/shared/protocols/public/fish/Fish';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { FishWhaleGameMgr } from '../../../FishWhaleGameMgr';
import { MsgFireResult } from 'db://assets/module_basic/shared/protocols/public/fish/MsgFireResult';
import { Logger } from 'db://assets/core_tgx/base/log/Logger';
const { ccclass, property } = _decorator;

export class waitBackBullet
{
    bulletID:number;
    bulletLevel:number;
    startPos:Vec3;
    endPos:Vec3;
    constructor(bulletID: number, bulletLevel: number,startPos:Vec3,endPos:Vec3) {
        // 将传入的参数赋值给类的属性
        this.bulletID = bulletID;
        this.bulletLevel = bulletLevel;
        this.startPos = startPos;
        this.endPos = endPos;
    }
}


@ccclass('bulletMgrCmpt')
export class bulletMgrCmpt extends Component {
    private bulletPre: Prefab = null;
    private bulletList: Node[] = [];
    private _bulletID:number = 0;
    private _fireTime: number = 0;
    private _fireTimeNew: number;
    private _waitBackBulletMap:Map<number,waitBackBullet> = new Map();
    async onLoad() {
        App.event.on(EventName.Game.BulletDied, this.evtBulletDied, this);
        App.event.on(EventName.Game.HitFish, this.evtBulletHitFish, this);
        App.event.on(EventName.Game.CreateOldBullet,this.createOldBullet,this);
        
        
    }

    onEnable() {
       

    }
    async start()
    {
        this.bulletPre = await ResLoadHelper.loadBullet();
        if(SubGameMgr.gameMgr)
        {
            var initData:any[] = (SubGameMgr.gameMgr as FishWhaleGameMgr).getInitData();
            var bulletList:Array<Bullet> = initData[2];
            var lastBulletId:number = initData[3];
            this.createOldBullet(bulletList,lastBulletId);
        }
        this.initNetMsgHandlers();
    }
    onDestroy(): void {
        App.event.off(EventName.Game.BulletDied, this);
        App.event.off(EventName.Game.HitFish, this);
        App.event.off(EventName.Game.CreateOldBullet,this);
        gameNet.unlistenMsgAllByThis(this);
    }

    private initNetMsgHandlers() 
    {
        gameNet.listenMsg("fish/FireResult", this.onNet_FireResult, this);
    }

    evtBulletDied() {
        this.bulletList.shift();
    }

    getOneBullet() {
        let bullet = instantiate(this.bulletPre);
        this.node.addChild(bullet);
        return bullet;
    }
    
    shoot(startPos: Vec3, aimV: Vec3) {
        var needCoin:number = FishWhaleGameMgr.inst.getBulletCoin(App.gameLogic.paoLevel);
        if(needCoin == -1)
        {
            Logger.trace("等待配置返回")
            return;
        }
        this._fireTimeNew = new Date().getTime();
        if(this._fireTimeNew - this._fireTime < 100){
            return;
        }
        if (this.bulletList.length > Constant.SceneTotalBullet) {
            App.view.showMsgTips("子弹突破上限啦!")
            return
        }
       
        if(needCoin > App.gameLogic.gold)
        {
            App.view.showMsgTips("金币不足");
            return;
        }
        let bulletID:number = this.generateBulletID();
        this._waitBackBulletMap.set(bulletID,new waitBackBullet(bulletID,App.gameLogic.paoLevel,startPos,aimV));
        this.sendFire(App.gameLogic.paoLevel,bulletID);
    }
    private createBullet(data:waitBackBullet) {
        
        let bulletID:number = data.bulletID;
        let bullet = this.getOneBullet();
        bullet.active = true;
        bullet.setPosition(data.startPos);
        let bScript = bullet.getComponent(bulletCmpt);
        bScript.bulletId = bulletID;
        bScript.bulletLev = data.bulletLevel;
        bScript.startMove(v2(data.startPos.x, data.startPos.y), v2(data.endPos.x, data.endPos.y));
        this.bulletList.push(bullet);
    }
    //生成子弹ID
    private generateBulletID():number
    {
        this._bulletID++;
        return this._bulletID;
    }
    /**发射子弹 */
    private sendFire(bulletLevel:number,bulletId:number):void
    {
        var msg:MsgFire = {} as MsgFire;
        msg.bulletLevel = bulletLevel;
        msg.bulletId = bulletId.toString();
        console.log("sendFire bulletId:"+bulletId);
        gameNet.sendMsg('fish/Fire', msg);
    }

    private evtBulletHitFish(bulletId:number,bulletLevel:number,fishID:number):void
    {
        var msg:MsgHitFish = {} as MsgHitFish;
        msg.fishId = fishID.toString();
        msg.bulletId = bulletId.toString();
        msg.bulletLevel = bulletLevel;
        gameNet.sendMsg('fish/HitFish', msg);
    }
    private createOldBullet(oldBullets:Bullet[],lastBulletId:number):void
    {
        //生成旧的子弹 随机
        for(var i:number = 0;i<oldBullets.length;i++)
        {
            // 随机初始点
            var startPos:Vec3 = this.getRandomPosition();
            // 随机终点，确保和起点不同
            let endPos = this.getRandomPosition();
            while (this.isSamePosition(startPos, endPos)) {
                endPos = this.getRandomPosition();
            }

            let bullet = this.getOneBullet();
            bullet.active = true;
            bullet.setPosition(startPos);
            let bScript = bullet.getComponent(bulletCmpt);
            bScript.bulletId = Number(oldBullets[i].id);
            bScript.bulletLev = Number(oldBullets[i].bulletLevel);
            bScript.startMove(v2(startPos.x, startPos.y), v2(endPos.x, endPos.y));
            this.bulletList.push(bullet);
        }
        if (isNaN(lastBulletId)) {
            this._bulletID = 0;
        }
        else
        {
            this._bulletID = lastBulletId;
        }

        
    }
    private getRandomPosition(): Vec3 {
        const designResolution = view.getDesignResolutionSize();
        const x = (Math.random() - 0.5) * designResolution.width;
        const y = (Math.random() - 0.5) * designResolution.height;
        return new Vec3(x, y, 0);
    }
    private isSamePosition(pos1: Vec3, pos2: Vec3): boolean {
        return pos1.x === pos2.x && pos1.y === pos2.y;
    }

    private onNet_FireResult(msg:MsgFireResult):void
    {
        if(msg.isSuccess)
        {
            console.log("FireResult bulletId:"+msg.bulletId);
            App.gameLogic.gold = Number(msg.coins);
            App.event.emit(EventName.Game.UPDATE_COIN);
            if(this._waitBackBulletMap.has(Number(msg.bulletId)))
            {
                this.createBullet(this._waitBackBulletMap.get(Number(msg.bulletId)));
            }
            else
            {
                Logger.error("FireResult not found:"+msg.bulletId);
            }
        }
        else
        {
            Logger.error("FireResult:"+msg.reason);
        }
        this._waitBackBulletMap.delete(Number(msg.bulletId));
    }
}


