//----------------------------------------大厅

/** 窗口类型 */
export enum WindowType {
    /** 大厅 */
    eMap = 1,
    /** 窗口 */
    eView,
    /** 弹窗 */
    eTips,
    /** 跑马灯 */
    eMarquee,
    /** 飘字 */
    eToast,
    eNetwork,
}

/** 窗口打开类型 */
export enum WindowOpenType {
    /** 只能展示这个，立即打开 */
    eSingle = 1,
    /** 可以多个同时存在，一个关闭另一个再打开 */
    eMultiple,
}

