import { gameMgr } from './../../../../module_fish/scripts/game/gameMgr';
import { _decorator, Component, Node, instantiate, v3, UITransform, v2, Prefab, Vec3 } from 'cc';
import { Constant, LevelData } from '../../const/enumConst';
import { EventName } from '../../const/eventName';
import { LevelConfig } from '../../const/levelConfig';
import { App } from '../../core/app';
import { ResLoadHelper } from '../../utils/resLoadHelper';
import { StorageHelper, StorageHelperKey } from '../../utils/storageHelper';
import { BYFish } from './fishCmpt';
import { BYFishRoute } from './fishRoute';
import { gameNet } from 'db://assets/module_basic/scripts/NetGameServer';
import { MsgNewFish } from 'db://assets/module_basic/shared/protocols/public/fish/MsgNewFish';
import { MsgFishDead } from 'db://assets/module_basic/shared/protocols/public/fish/MsgFishDead';
import { MsgRoomFish } from 'db://assets/module_basic/shared/protocols/public/fish/MsgRoomFish';
import { Vec2 } from 'cc';
import { SubGameManager } from '../../core/subGameManager';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { FishWhaleGameMgr } from '../../../FishWhaleGameMgr';
import { Bullet } from 'db://assets/module_basic/shared/protocols/public/fish/Fish';
const { ccclass, property } = _decorator;

export class FishLive
{
    fish:BYFish;
    cliveTimestamp:number;
    constructor(fish: BYFish, cliveTimestamp: number) {
        // 将传入的参数赋值给类的属性
        this.fish = fish;
        this.cliveTimestamp = cliveTimestamp;
    }
}

@ccclass('fishMgrCmpt')
export class fishMgrCmpt extends Component {
    private fishPreMap: Map<number, Prefab> = new Map();
    private data: LevelData = null;
    private _fishList: Map<number,FishLive>; //鱼 生成时间map
    onLoad() {
        
    }
    start() {
        this._fishList = new Map();
        this.initNetMsgHandlers();
        App.event.on(EventName.Game.FishDied, this.evtFishDied, this);
        if(SubGameMgr.gameMgr)
        {
            var initData:any[] = (SubGameMgr.gameMgr as FishWhaleGameMgr).getInitData();
            var fishList:Array<MsgNewFish> = initData[0];
            for(var i:number=0;i<fishList.length;i++)
            {
                this.onNet_NewFish(fishList[i]);
            }
        }
    }

    onEnable() {
        
        this.refreshFishData();
        
    }
    onDisable() {
       
    }
    onDestroy(): void {
        App.event.off(EventName.Game.FishDied, this);
        gameNet.unlistenMsgAllByThis(this);
    }
    update(deltaTime: number) {
        //计算鱼是否要从map里删除 依赖服务器生存时间
        //this._updateUserLevelUI();
        if(gameNet.serverTimestamp >0)
        {
            var keysToDelete: number[] = []; 
            for (const key of this._fishList.keys()) {
                if(this._fishList.get(key).cliveTimestamp < gameNet.serverTimestamp)
                {
                    keysToDelete.push(key);
                }
            }
            for (const key of keysToDelete) {
                if(this._fishList.get(key).fish)
                {
                    this._fishList.get(key).fish.destroyFish();
                }
                this._fishList.delete(key);
            }
        }
        
    }

    private initNetMsgHandlers() 
    {
        gameNet.listenMsg("fish/NewFish", this.onNet_NewFish, this);
        gameNet.listenMsg("fish/FishDead", this.onNet_FishDead, this);
        gameNet.listenMsg("fish/RoomFish", this.onNet_RoomFish, this);
    }


    refreshFishData() {
        let data: LevelData = LevelConfig.getDefaultData();
        this.data = data;

        
    }

    /** 创建鱼接口 */
    async createFishById(fishType: number,fishID:number,liveTimestamp:number) {
        let rount = BYFishRoute.anchor;
        let routeId = rount[Math.floor(Math.random() * (rount.length - 1))];
        let fish = await this.createFish(fishType, routeId.id, 0, fishID, this.node);
        if (!fish) return;
        this._fishList.set(fishID,new FishLive(fish,liveTimestamp));

    }

    /**
     * 创建一条普通鱼          
     * @param fishType 类型
     * @param rootIdx 路线ID
     * @param offsetID 偏移ID
     * @param fishId 该鱼的ID
     * @param startPosNum 
     * @param firstTime 
     */
    public async createFish(fishType: number, rootIdx: number, offsetID: number, fishId: number, parentNode: Node) {
        if (!fishId) return;
        for (let i = 0; i < BYFishRoute.anchor.length; i++) {
            if (BYFishRoute.anchor[i].id == rootIdx) {
                rootIdx = i;
                break;
            }
        }

        if (rootIdx == undefined) {
            console.log("rootIdx  is  undifen");
            return;
        }
        //初始位置
        let pos = v3(BYFishRoute.anchor[rootIdx].points[0][0] + BYFishRoute.offset[offsetID].x,
            BYFishRoute.anchor[rootIdx].points[0][1] + BYFishRoute.offset[offsetID].y);
        let fish: BYFish = await this.createOneFishByFishType(fishType, pos, parentNode);

        if (fish === undefined) {
            console.log('fishType = ', fishType)
            console.log("fish =  undefined");
            return;
        }
        let fishNode = fish.node;
        fishNode.name = fishId.toString();//tag->name
        fish.fishFormationId = -1;
        fish.rootIdx = rootIdx;
        fish.typeId = fishType;
        fish.fishId = fishId;


        let point = v2(BYFishRoute.offset[offsetID].x, BYFishRoute.offset[offsetID].y);
        let xstartPosNum = 0;
        let curveTimex = BYFishRoute.anchor[rootIdx].curveTime;
        let xfirstTime = curveTimex;
        fish.move(point, xstartPosNum, xfirstTime);
        return fish;
    }

   // 创建鱼
   private async createOneFishByFishType(fishType: number, pos: Vec3, parentNode: Node)
    {
        let pre = this.fishPreMap.get(fishType);
        let fishPre = pre || await ResLoadHelper.loadFish(fishType);
        if (!pre) {
            this.fishPreMap.set(fishType, fishPre);
        }
        if (fishPre) {
            let fishNode = instantiate(fishPre);
            parentNode.addChild(fishNode);
            fishNode.setPosition(pos);

            let fish: BYFish = fishNode.getComponent(BYFish);
            if (!fish) {
                fish = fishNode.addComponent(BYFish);
            }
            fish.enabled = true;
            fish.chgColliderState(true);
            fish.fishId = fishType;
            fish.isDieing = false;
            fish.chgIceState(false);
            fish.chgSpineState(true);
            return fish;
        }
        return null;
    }

    /** 将死鱼移除 */
    evtFishDied(id: number) {
        let fishItemComp = this._fishList.get(id);
        if(fishItemComp)
        {
            fishItemComp.fish = null;
        }
    }

    handleFishLock() {
        // for (let i = 0; i < this.fishList.length; i++) {
        //     this.fishList[i].showBtn();
        // }
    }

    private onNet_NewFish(msg:MsgNewFish)
    {
        this.createFishById(msg.type,Number(msg.id),msg.liveTimestamp);
    }
    private onNet_FishDead(msg:MsgFishDead)
    {
        let fishItemComp = this._fishList.get(Number(msg.id));
        if(fishItemComp)
        {
            if(fishItemComp.fish)
            {
                fishItemComp.fish.playDie();
                App.gameLogic.gold = msg.coins;
                App.event.emit(EventName.Game.UPDATE_COIN);
                
                let fishPosCache = new Vec3();
                fishItemComp.fish.node.getPosition(fishPosCache);
                let vec3 = new Vec3(fishPosCache.x, fishPosCache.y);
                App.event.emit(EventName.Game.FLY_COIN,msg.incrementCoins,vec3);
            }
            
        }   
    }
    private onNet_RoomFish(msg:MsgRoomFish)
    {
        for(var i:number = 0;i<msg.fishes.length;i++)
        {
            if(this._fishList.get(Number(msg.fishes[i].id)))
            {
                continue; 
            }
            this.onNet_NewFish(msg.fishes[i]);
        }
        if(msg.bullets)
        {
            App.event.emit(EventName.Game.CreateOldBullet,msg.bullets,msg.lastBulletId);
        }
        
    }
}



