import { _decorator, Component, Node, Vec2, director, v3, view, Collider2D, CircleCollider2D, Contact2DType, IPhysics2DContact, RigidBody2D, isValid } from 'cc';
import { EventName } from '../../const/eventName';
import { App } from '../../core/app';
import { deg2Rad, getQuadrantDegree } from '../../net/util';
const { ccclass, property } = _decorator;

@ccclass('bulletCmpt')
export class bulletCmpt extends Component {
    private speed = 15;
    private speedX = 0;
    private speedY = 0;
    public bulletId: number = 0;

    public halfSW: number = 0;
    public halfSH: number = 0;

    private collider: Collider2D = null;
    public isDistoried: boolean = false;
    private _bulletLev:number = 0;

    onLoad() {
        this.initColliderAndShader();
        let winSize = view.getVisibleSize();
        this.halfSW = winSize.width / 2;
        this.halfSH = winSize.height / 2;
    }

    set bulletLev(data:number) {
        this._bulletLev = data;
        let index = this._bulletLev;
        this.node.children.forEach(item => item.active = false);
        this.node.getChildByName("" + index).active = true;
    }
    get bulletLev():number {
        return this._bulletLev;
    }

    showOne() {
        let len = this.node.children.length;
        let idx = Math.floor(Math.random() * len);
        this.node.children.forEach(item => item.active = false);
        this.node.children[idx].active = true;
    }

    initColliderAndShader() {
        this.collider = this.node.getComponent(Collider2D);
        if (!this.collider) {
            this.collider = this.node.getComponent(CircleCollider2D);
        }
        if (this.collider) {
            this.collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            this.collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }
    }

    chgColliderState(state: boolean) {
        if (this.collider) {
            this.collider.enabled = state;
            this.node.getComponent(RigidBody2D).enabledContactListener = state;
        }
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体开始接触时被调用一次
        
    }

    onDisable() {
        
    }

    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        // console.log('onEndContact');
    }

    //开始移动
    public startMove(spawnV: Vec2, aimV: Vec2) {
        let deg = getQuadrantDegree(spawnV, aimV);
            //一四象限
        if (deg>= 0) {
            deg = -deg;
        } else {
            //二三象限
            deg = (-deg - 180);
        }
        this.node.angle = -deg - 90;
        //计算初始速度
        let rad = deg2Rad(deg);
        this.speedX = Math.cos(rad) * this.speed;
        this.speedY = Math.sin(rad) * this.speed;
        let absSpx = Math.abs(this.speedX);
        let absSpy = Math.abs(this.speedY);

        if (aimV.x > spawnV.x) {
            this.speedX = absSpx;
        } else {
            this.speedX = -absSpx;
        }
        if (aimV.y > spawnV.y) {
            this.speedY = absSpy;
        } else {
            this.speedY = -absSpy;
        }
    }

    update() {
        if (this.isDistoried) return;
        this.move();
    }

    private move() {
        let curX = this.node.position.x;
        let curY = this.node.position.y;
        let pos = v3(this.node.position.x, this.node.position.y, this.node.position.z);

        if (curX + this.speedX < -this.halfSW) {
            pos.x = -this.halfSW
            this.speedX = -this.speedX;
            //console.log("角度变化");
            this.node.angle = -this.node.angle;

        } else if (curX + this.speedX > this.halfSW) {
            pos.x = this.halfSW
            this.speedX = -this.speedX;
            this.node.angle = -this.node.angle;

        } else if (curY + this.speedY > this.halfSH) {
            pos.y = this.halfSH
            this.speedY = -this.speedY;
            this.node.angle = -(180 + this.node.angle) % 360;
        } else if (curY + this.speedY < -this.halfSH) {
            pos.y = -this.halfSH
            this.speedY = -this.speedY;
            this.node.angle = -(180 + this.node.angle) % 360;
        } else {
            pos.x = (curX + this.speedX);
            pos.y = (curY + this.speedY);
        }
        this.node.setPosition(pos);
    }

}


