import { _decorator, Component, Node, Vec3, v3, tween, Tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('test')
export class test extends Component {
    start() {
        this.scheduleOnce(() => {
            // let arr = [this.bezierTo(this.node.getChildByName('11'), 5, v3(), v3(100, 100, 1), v3(200, 0, 1))]
            let arr = [
                this.bezierTo(this.node.getChildByName('11'), 5, v3(), v3(100, 100, 1), v3(200, 0, 1)),
                this.bezierTo(this.node.getChildByName('11'), 5, v3(200, 0, 1), v3(100, -100, 1), v3(-200, 0, 1)),
                this.bezierTo(this.node.getChildByName('11'), 5, v3(-200, 0, 1), v3(100, -300, 1), v3()),
            ]
            this.startMove(arr);
        }, 2);
    }


    private index = 0;
    startMove(arr: any[]) {
        arr[this.index].call(() => {
            this.index++;
            if (this.index < arr.length) {
                this.startMove(arr);
            }
            else {
                // this.index = 0;
                // this.startMove(arr);
            }
        }).start();
    }

    public bezierTo(target: any, duration: number, c1: Vec3, c2: Vec3, to: Vec3, opts?: any) {
        opts = opts || Object.create(null);
        /*
        * @desc 二阶贝塞尔
        * @param {number} t 当前百分比
        * @param {} p1 起点坐标
        * @param {} cp 控制点
        * @param {} p2 终点坐标
        * @returns {any}
        */
        let twoBezier = (t: number, p1: Vec3, cp: Vec3, p2: Vec3) => {
            let x = (1 - t) * (1 - t) * p1.x + 2 * t * (1 - t) * cp.x + t * t * p2.x;
            let y = (1 - t) * (1 - t) * p1.y + 2 * t * (1 - t) * cp.y + t * t * p2.y;
            return v3(x, y, 0);
        };
        opts.onUpdate = (arg: Vec3, ratio: number) => {
            target.position = twoBezier(ratio, c1, c2, to);
        };
        return tween(target).to(duration, {}, opts)
    }
}


