import { _decorator, Component, Node, v3, tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('fishNetCmpt')
export class fishNetCmpt extends Component {
    onLoad() {
        this.showOne();
        this.node.setScale(v3());
        tween(this.node).to(0.3, { scale: v3(1, 1, 1) }, { easing: 'backOut' }).call(() => {
            tween(this.node).to(0.3, { scale: v3() }, { easing: 'backOut' }).start();
        }).start();
    }

    showOne() {
        let len = this.node.children.length;
        let idx = Math.floor(Math.random() * len);
        this.node.children.forEach(item => item.active = false);
        this.node.children[idx].active = true;
    }
}