import { _decorator, Node, instantiate, tween, v3 } from 'cc';
import { BaseViewCmpt } from '../../components/baseViewCmpt';
import { LevelData } from '../../const/enumConst';
import { EventName } from '../../const/eventName';
import { LevelConfig } from '../../const/levelConfig';
import { ViewName } from '../../const/viewNameConst';
import { App } from '../../core/app';
import { CocosHelper } from '../../utils/cocosHelper';
import { GlobalFuncHelper } from '../../utils/globalFuncHelper';
import { StorageHelper, StorageHelperKey } from '../../utils/storageHelper';
import { ToolsHelper } from '../../utils/toolsHelper';
import { Advertise } from '../../wx/advertise';
const { ccclass, property } = _decorator;

@ccclass('resultViewCmpt')
export class ResultViewCmpt extends BaseViewCmpt {
    private lbRefresh: Node = null;
    private lbGold: Node = null;
    onLoad() {
        super.onLoad();
        this.lbRefresh = this.viewList.get('animNode/lose/lbRefresh')
        this.lbGold = this.viewList.get('animNode/win/t/lbGold')
    }

    loadExtraData(isWin: boolean, starScore: number) {
        this.viewList.get('animNode/win').active = isWin;
        this.viewList.get('animNode/lose').active = !isWin;
        if (isWin) {
            let data: LevelData = LevelConfig.getDefaultData();
            for (let i = 0; i < data.targetList.length; i++) {
                App.gameLogic.addMyFeedFish(+data.targetList[i]);
            }
            LevelConfig.nextLevel();
            let count = +StorageHelper.getData(StorageHelperKey.StarScore, 0);
            StorageHelper.setData(StorageHelperKey.StarScore, count + starScore);
            this.playF();
        }
        this.scheduleOnce(() => {
            if (isWin) {
                App.audio.play('win');
            }
            else {
                App.audio.play('lose');
            }
        }, 0.2);

        let count = +StorageHelper.getData(StorageHelperKey.RefreshTools, 0);
        CocosHelper.updateLabelText(this.lbRefresh, count);
    }

    onClick_videoBtn() {
        Advertise.showVideoAds();
    }

    onClick_nextBtn() {
        this.onClick_closeBtn();
        App.event.emit(EventName.Game.NextLevel);
        let id = LevelConfig.getCurLevel();
        if (id > 2) {
            Advertise.showVideoAds();
        }
    }

    onClick_homeBtn() {
        App.view.openView(ViewName.Single.eFeedView);
        this.onClick_closeBtn();
    }

    onClick_replayBtn() {
        let count = +StorageHelper.getData(StorageHelperKey.RefreshTools, 0);
        if (count < 1) {
            let lv = LevelConfig.getCurLevel();
            App.event.emit(EventName.Game.Share, lv);
            App.view.showMsgTips("次数不足，分享可获取次数")
            return;
        }
        this.onClick_nextBtn();
        Advertise.showVideoAds();
    }

    async playF() {
        await ToolsHelper.delayTime(0.2);
        for (let i = 0; i < 10; i++) {
            this.playFlyCoin(0.5 + (i + 1.5) / 5);
        }
    }

    playFlyCoin(time: number) {
        let coin = instantiate(this.viewList.get('animNode/win/spCoin'));
        coin.active = true;
        this.viewList.get("animNode/win").addChild(coin);
        let p = this.viewList.get('animNode/win/pos1').position;
        coin.setPosition(v3(-50 + Math.random() * 100 + p.x, -50 + Math.random() * 100 + p.y, p.z));
        let endPos = this.viewList.get('animNode/win/pos2').position;
        tween(coin).to(time, { position: endPos }, { easing: 'backIn' }).call(() => {
            GlobalFuncHelper.setGold(10);
            CocosHelper.updateLabelText(this.lbGold, GlobalFuncHelper.getGold());
            coin.destroy();
            App.event.emit(EventName.Game.UpdataGold);
        }).start();
    }
}


