import { _decorator, Component, Node } from 'cc';
import { EventName } from '../../const/eventName';
import { App } from '../../core/app';
const { ccclass, property } = _decorator;

@ccclass('fireCmpt')
export class fireCmpt extends Component {
    private index = -1;
    private count: number = 0;
    private isFire: boolean = false;
    onLoad() {
        this.evtChangePaotai();
        App.event.on(EventName.Game.ChangePaotai, this.evtChangePaotai, this);
    }
    evtChangePaotai() {
        this.index = App.gameLogic.paoLevel;
        this.node.children.forEach(item => item.active = false);
        this.node.getChildByName("" + this.index).active = true;
    }

    update(deltaTime: number) {
        if (this.index == -1) return;
        if (!this.isFire) {
            this.node.getChildByName("" + this.index).active = false;
            return;
        }
        this.count++;
        if (this.count > 5) {
            this.node.getChildByName("" + this.index).active = true;
            this.count = 0;
            return;
        }
        this.node.getChildByName("" + this.index).active = false;
    }

    playFire() {
        this.isFire = true;
    }
    pause() {
        this.isFire = false;
    }

    onDisable() {
        App.event.off(EventName.Game.ChangePaotai, this);
    }
}