import { _decorator, Node, Vec3, Prefab, instantiate } from 'cc';
import { BaseViewCmpt } from '../../components/baseViewCmpt';
import { Constant } from '../../const/enumConst';
import { EventName } from '../../const/eventName';
import { LevelConfig } from '../../const/levelConfig';
import { ViewName } from '../../const/viewNameConst';
import { App } from '../../core/app';
import { CocosHelper } from '../../utils/cocosHelper';
import { GlobalFuncHelper } from '../../utils/globalFuncHelper';
import { ResLoadHelper } from '../../utils/resLoadHelper';
import { StorageHelper, StorageHelperKey } from '../../utils/storageHelper';
import { Advertise } from '../../wx/advertise';
import { BYFishRoute } from '../fish/fishRoute';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { SubGameDef } from 'db://assets/module_basic/scripts/SubGameDef';
import { Label } from 'cc';
import { UserMgr } from 'db://assets/module_basic/scripts/UserMgr';
import { GameLogic } from '../logic/gameLogic';
const { ccclass, property } = _decorator;

@ccclass('homeViewCmpt')
export class homeViewCmpt extends BaseViewCmpt {
     
    
    onLoad() {
        super.onLoad();
        this.initData();
        App.event.on(EventName.Game.UpdataGold, this.initData, this);
        this.viewList.get("bottom").active = !App.gameLogic.isShenhe;
    }

    onDestroy() {
        App.event.off(EventName.Game.UpdataGold, this);
    }

    initData(bool: boolean = true) {
        Advertise.showBannerAds();
        this.initFish();
        // let guid = StorageHelper.getBooleanData(StorageHelperKey.GuidFirst);
        // if (!guid) {
        //     StorageHelper.setBooleanData(StorageHelperKey.GuidFirst, true);
        //     App.view.openView(ViewName.Single.eHelpView);
        // }
    }

    initFish() {
        this.schedule(async () => {
            let rount = BYFishRoute.anchor;
            let routeId = rount[Math.floor(Math.random() * (rount.length - 1))];
            let fId = Math.ceil(Math.random() * (Constant.TotalFishCount - 30));
            let fish = await App.gameLogic.createFish(fId, routeId.id, 0, fId, this.viewList.get('bg'));
        }, 1, 5);
    }
    /* *************************** main ****************************** */

    onClick_settingBtn() {
        App.audio.play('button_click');
        App.view.openView(ViewName.Single.eSettingView);
    }

    onClick_playBtn() {
        App.audio.play('button_click');
        //App.view.openView(ViewName.Single.eGameView);
        //this.onClick_closeBtn();
        SubGameMgr.inst.CreateRoom(SubGameDef.FISH_WHALE,true);
    }
    onClick_feedBtn() {
        App.audio.play('button_click');
        App.view.openView(ViewName.Single.eFeedView);
        this.onClick_closeBtn();
    }

    onClick_shopBtn() {
        App.audio.play('button_click');
        App.view.openView(ViewName.Single.eBuyView);
    }

    /** 购买 */
    onClick_add() {
        App.audio.play('button_click');
        Advertise.showVideoAds();
        App.view.openView(ViewName.Single.eBuyView);
    }

    onClick_luckSpinBtn() {
        App.audio.play('button_click');
        App.view.openView(ViewName.Single.eLuckSpinView);
    }
    onClick_signBtn() {
        App.audio.play('button_click');
        App.view.openView(ViewName.Single.eSiginView);
    }
    onClick_onlineBtn() {
        App.audio.play('button_click');
        App.view.openView(ViewName.Single.eOnlineRewardView);
    }

    onClick_helpBtn() {
        // App.audio.play('button_click');
        // App.view.openView(ViewName.Single.eHelpView);
    }


}


