import { _decorator, Component, Node, tween, UIOpacity, Label, v3 } from 'cc';
import { BaseViewCmpt } from '../../components/baseViewCmpt';
import { LevelData } from '../../const/enumConst';
import { LevelConfig } from '../../const/levelConfig';
import { ToolsHelper } from '../../utils/toolsHelper';
const { ccclass, property } = _decorator;

@ccclass('targetViewCmpt')
export class targetViewCmpt extends BaseViewCmpt {
    private list: Node[] = [];
    private target: Node = null;
    onLoad() {
        super.onLoad();
        this.list = this.viewList.get('target').children;
        this.target = this.viewList.get('target');
        this.scheduleOnce(() => {
            tween(this.node.getComponent(UIOpacity)).to(1, { opacity: 0 }).call(() => {
                this.onClick_closeBtn();
            }).start();
        }, 3);
    }

    /** 初始化 */
    async loadExtraData() {
        let data: LevelData = LevelConfig.getDefaultData();
        this.list.forEach(item => item.active = false);
        await ToolsHelper.delayTime(0.3);
        for (let i = 0; i < data.targetList.length; i++) {
            let name = data.targetList[i];
            let item = this.target.getChildByName(name + "");
            console.log("name = " + name);
            item.active = true;
            item.getChildByName('lb').getComponent(Label).string = data.num + "";
        }
    }
}


