import { _decorator, Node, Vec2, v3 } from 'cc';
import { BaseViewCmpt } from '../../components/baseViewCmpt';
import { EventName } from '../../const/eventName';
import { LevelConfig } from '../../const/levelConfig';
import { ViewName } from '../../const/viewNameConst';
import { App } from '../../core/app';
import { GlobalFuncHelper } from '../../utils/globalFuncHelper';
import { fishMgrCmpt } from '../fish/fishMgrCmpt';
import { BYFishRoute } from '../fish/fishRoute';
const { ccclass, property } = _decorator;

@ccclass('feedViewCmpt')
export class feedViewCmpt extends BaseViewCmpt {
    private noFishTips: Node = null;
    private fishManager: fishMgrCmpt = null;
    onLoad() {
        super.onLoad();
        this.noFishTips = this.viewList.get("center/noFishTips");

        let rand = 2;//Math.ceil(Math.random() * 4);
        this.viewList.get("bg").children.forEach(item => {
            item.active = +item.name == rand;
        })
    }

    addEvents() {
        App.event.on(EventName.Game.TouchStart, this.evtTouchStart.bind(this), this);
        App.event.on(EventName.Game.TouchMove, this.evtTouchMove.bind(this), this);
        App.event.on(EventName.Game.TouchEnd, this.evtTouchEnd.bind(this), this);
        // App.event.on(EventName.Game.UpdataGold, this.evtUpdateGold, this);
    }
    onDisable() {
        App.event.off(EventName.Game.TouchStart, this);
        App.event.off(EventName.Game.TouchMove, this);
        App.event.off(EventName.Game.TouchEnd, this);
    }


    /** 初始化 */
    async loadExtraData() {
        this.initFish();
    }

    initFish() {
        let fishIdList: number[] = App.gameLogic.getMyFeedFish();
        this.noFishTips.active = false;
        if (fishIdList.length < 1) {
            this.noFishTips.active = true;
            return;
        }
        /** 每种鱼养3条先 */
        let count = 0;
        this.schedule(async () => {
            let rount = BYFishRoute.anchor;
            let routeId = rount[Math.floor(Math.random() * (rount.length - 1))];
            let fId = fishIdList[count]//fishIdList[Math.floor(Math.random() * (fishIdList.length))];
            let fish = await App.gameLogic.createFish(fId, routeId.id, 0, fId, this.viewList.get('bg'));
            count++;
            if (count > fishIdList.length - 1) {
                count = 0;
            }
        }, 1, fishIdList.length * 3);
    }

    evtTouchStart(pos: Vec2) {
    }

    evtTouchMove(pos: Vec2) {
    }


    evtTouchEnd() {
    }

    /** 退出 */
    onClick_backBtn() {
        App.backHome();
    }

    onClick_catchBtn() {
        App.view.openView(ViewName.Single.eGameView);
        this.onClick_closeBtn();
    }

    private isHide: boolean = false;
    onClick_hideBtn() {
        this.isHide = !this.isHide;
        this.viewList.get("ui/right/hideBtn").scale = this.isHide ? v3(1, -1, 1) : v3(1, 1, 1);
        this.viewList.get("ui/right").children.forEach(item => {
            if (item.name != "hideBtn") {
                item.active = !this.isHide;
            }
        })
    }
}


